import React from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import './SoundWave.css';

const SoundWave = ({ width, height, style, fillColor, isActive, className }) => (
  <svg
    style={style}
    width={width}
    height={height}
    aria-hidden='true'
    viewBox='0 0 48 48'
    xmlns='http://www.w3.org/2000/svg'
    className={classnames(className, isActive && 'sound-wave--active')}
  >
    <g fill={fillColor}>
      <rect className='sound-wave-bar sound-wave-bar-1' x='4.9' rx='1.5' />
      <rect className='sound-wave-bar sound-wave-bar-2' x='13.5' rx='1.5' />
      <rect className='sound-wave-bar sound-wave-bar-3' x='22.1' rx='1.5' />
      <rect className='sound-wave-bar sound-wave-bar-2' x='30.7' rx='1.5' />
      <rect className='sound-wave-bar sound-wave-bar-1' x='39.5' rx='1.5' />
    </g>
  </svg>
);

SoundWave.defaultProps = {
  width               : '24',
  height              : '24',
  fillColor           : '#fff'
};

SoundWave.propTypes = {
  style               : PropTypes.object,
  width               : PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  height              : PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  className           : PropTypes.string,
  fillColor           : PropTypes.string
};

export default SoundWave;
